exports.getSidebarCategoryList = () => {
  let x =
    Object.entries(this.articles)
      .map(([k, v]) => [k, v.chapters.map(c => c.startsWith('[') ? c : (v.root + '/' + c))])
      .reduce((p, c) => {p[c[0]] = c[1]; return p;}, {});
  return x;
}
